import { useContext, Fragment } from 'react';
import { useNavigate } from 'react-router-dom';
import { NavigatorStyled, LinkStyled, LogoutButton, GroupStyled, MemberStyled } from './styled';

import { AuthContext } from '../../../contexts/auth.context';

// import styled from 'styled-components';
const defaultRouter = {
	member: { route: '/dashboard/member', name: '' },
	'member-change': { route: '/dashboard/member-change', name: '' },
	plugin: { route: '/dashboard/plugin', name: '' },
	admin: { route: '/dashboard/admin', name: '' },
};

function Navigator({ collapsed }) {
	const { user, logout } = useContext(AuthContext);
	const { name, mail, login_at, router } = user;
	const navigate = useNavigate();

	// 
	const handleLogout = () => {
		logout();
		navigate('/login'); // 
	};

	return (
		<NavigatorStyled $collapsed={collapsed}>
			{user.mail !== '' ? (
				<Fragment>
					<GroupStyled>
						<MemberStyled>{name !== '' ? name : ''}</MemberStyled>
						<LogoutButton onClick={handleLogout}></LogoutButton>
					</GroupStyled>
					<GroupStyled>
						{router &&
							router.map((route) => {
								return (
									<LinkStyled to={defaultRouter[route].route}>
										{defaultRouter[route].name}
									</LinkStyled>
								);
							})}
					</GroupStyled>
				</Fragment>
			) : (
				<GroupStyled>
					<LinkStyled to="/login">/</LinkStyled>
				</GroupStyled>
			)}
		</NavigatorStyled>
	);
}

export default Navigator;
