import React, { useState, useContext } from 'react';
import SendRequest from '../../../utils/auth-service.utils';
import FormInput from '../../../components/form-input';
import FormSelect from '../../../components/form-select';
import Button from '../../../components/button';
import Loading from '../../../components/loading';
import SafeFetch from '../../../utils/safe-fetch.utils';
import { MemberChangeMenuContext } from '../../../contexts/member-change-menu.contexts';
import FakeData from '../../../fake-data';
import {
	MemberChangeStyled,
	FormGroupStyled,
	FromItemStyled,
	MessageStyled,
	MemberInfoContainer,
	MemberName,
	AdminLink,
} from './styled';

function Menu() {
	const { formFields, handleChange, handleReset, memberData, setMemberData, handleTab } =
		useContext(MemberChangeMenuContext);
	const { email, brand, category } = formFields;
	const [loading, setLoading] = useState(false);
	const [message, setMessage] = useState('');
	const brands = [
		{ value: 'xuemi', label: '', page: 'https://www.xuemi.co/admin/members/' },
		{ value: 'sixdigital', label: '', page: 'https://www.ooschool.cc/admin/members/' },
		{ value: 'kkschool', label: '', page: 'https://kkschool.kolable.app/admin/members/' },
		{ value: 'nschool', label: '', page: 'https://nschool.tw/admin/members/' },
	];
	const categories = [
		{ value: 'search', label: '' },
		{ value: 'leave', label: '' },
		{ value: 'extension', label: '' },
		{ value: 'adjust-order', label: '' },
		{ value: 'adjust-coupon', label: '' },
		{ value: 'adjust-coin', label: '' },
	];
	const handleClear = async () => {
		handleReset();
		setMemberData({});
	};

	const handleSubmit = async () => {
		if (loading) return alert('Be more patient! please wait a moment.');
		if (!brand || !category || !email) {
			setMessage('');
			return;
		}
		setLoading(true);
		setMessage('');
		handleTab('');
		setMemberData({});
		const errors = [];

		try {
			//  memberid
			const getData = {
				do: 'search',
				category: category,
				mail: email,
				brand: brand,
				staffMail: JSON.parse(localStorage.getItem('memberApp')).email,
			};

			const result = await SafeFetch(() => SendRequest(getData), 'search not found');

			console.log(result);
			if (!result.success) throw new Error(result.message);

			handleTab();
			setMemberData(result.data);
		} catch (error) {
			errors.push(`${error.message}`);
		} finally {
			setLoading(false);
			if (errors.length > 0) setMessage(`:\n${errors}`);
			else setMessage('');
		}
	};

	return (
		<div>
			<h2></h2>
			<MemberChangeStyled>
				<FormGroupStyled>
					<FromItemStyled>
						<FormSelect
							label=""
							selectOptions={brands}
							value={brand}
							name="brand"
							onChange={handleChange}
						/>
					</FromItemStyled>
					<FromItemStyled>
						<FormSelect
							label=""
							selectOptions={categories}
							value={category}
							name="category"
							onChange={handleChange}
						/>
					</FromItemStyled>
					<FromItemStyled>
						<FormInput
							label=""
							inputOption={{
								type: 'email',
								required: true,
								onChange: handleChange,
								name: 'email',
								value: email,
							}}
						/>
					</FromItemStyled>
				</FormGroupStyled>
				<FormGroupStyled>
					<Button
						type="submit"
						onClick={handleSubmit}
						disabled={loading}
					>
						{loading ? <Loading /> : ''}
					</Button>
					<Button
						type="submit"
						onClick={handleClear}
						disabled={loading}
					>
						{loading ? <Loading /> : ''}
					</Button>
					<Button
						type="submit"
						onClick={() => {
							handleTab();
							setMemberData(FakeData);
						}}
						disabled={loading}
					>
						{loading ? <Loading /> : ''}
					</Button>
					{message && <MessageStyled>{message}</MessageStyled>}
				</FormGroupStyled>
			</MemberChangeStyled>
			<MemberInfoContainer>
				{memberData?.member?.name && <MemberName>{memberData.member.name}</MemberName>}

				{memberData?.member && (
					<span>
						<AdminLink
							to={brands.find((e) => e.value === brand)?.page + memberData.member.id}
							target="_blank"
							rel="noopener noreferrer"
						>
							
						</AdminLink>
					</span>
				)}
			</MemberInfoContainer>
		</div>
	);
}

export default Menu;
